.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  background: linear-gradient(0deg, rgba(255, 61, 0, .2) 33%, #ff3d00 100%);
  border-radius: 50%;
  justify-content: center;
  justify-self: center;
  margin-top: 100px;
  animation: rotation 1s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 44px;
  height: 44px;
  background: #263238;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.isHidden {
  display: none;
}

